import React from "react";
import Backdrop from "./Backdrop";

const PageLoading = (props) => {
  return (
    <>
      <Backdrop show={true} clicked={null} />
      <div className="Modal rounded shadow pageLoading">
        <div className="loader"></div>
        <div className="my-4 d-inline-block">در حال بارگذاری</div>
      </div>
    </>
  );
};

export default PageLoading;
