import React, { Component } from "react";
import SelectedIngredient from "./SelectedIngredient";

class Material extends Component {
  //onClickHandler = () => {};
  state = { isImgLoaded: false };

  onLoadedHandler = () => {
    this.setState({ isImgLoaded: true });
  };

  render() {
    const imgAddress = process.env.PUBLIC_URL + "/images/" + this.props.img;

    let content = null;
    if (!this.state.isImgLoaded) {
      content = (
        <>
          <div className="loader2 mx-auto"></div>
          <img
            src={imgAddress}
            className=" ingredientImg mt-4 rounded-circle"
            style={{ display: "none" }}
            onLoad={this.onLoadedHandler}
          />
        </>
      );
    } else {
      content = (
        <>
          <SelectedIngredient {...this.props} />
          <img
            src={imgAddress}
            className=" ingredientImg mt-4 rounded-circle"
          />
          <div className="text-center m-3 pt-2 pb-2">{this.props.name}</div>
        </>
      );
    }

    return (
      <>
        <div
          className=" ingredientContainer rounded shadow"
          onClick={this.state.isImgLoaded ? this.props.clicked : null}
        >
          {content}
        </div>
      </>
    );
  }
}

export default Material;
